import React, { Component } from "react";
import "./App.css";
import ListItems from "./component/ListItems";
import Particles from "react-particles-js";

const particlesOptions = {
  particles: {
    number: {
      value: 30,
      density: {
        enable: true,
        value_area: 100
      }
    }
  }
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      countTaskLeft: 0,
      items: [],
      currentItem: {
        text: "",
        key: "",
        completed: false
      }
    };
    this.handelInput = this.handelInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.setUpdate = this.setUpdate.bind(this);
    this.finishItem = this.finishItem.bind(this);
    this.tasksLeft = this.tasksLeft.bind(this);
  }

  handelInput = e => {
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now(),
        completed: false
      }
    });
  };

  addItem = e => {
    e.preventDefault();
    const newItem = this.state.currentItem;
    //console.log(newItem);
    if (newItem.text !== "") {
      const newItems = [...this.state.items, newItem];
      this.setState({
        items: newItems,
        currentItem: {
          text: "",
          key: "",
          completed: false
        }
      });
    }
    //this.tasksLeft();
  };

  deleteItem = key => {
    const filteredItems = this.state.items.filter(item => item.key !== key);
    this.setState({
      items: filteredItems
    });
    this.tasksLeft();
  };

  setUpdate = (text, key) => {
    const items = this.state.items;
    items.map(item => {
      if (item.key === key) {
        item.text = text;
      }
    });
    this.setState({
      items: items
    });
  };

  finishItem = key => {
    const items = this.state.items;
    items.map(item => {
      if (item.key === key) {
        item.completed = !item.completed;
      }
    });
    this.setState({
      items: items
    });
    this.tasksLeft();
  };

  tasksLeft = () => {
    const filteredItems = this.state.items.filter(
      item => item.completed === false
    );
    this.setState({ countTaskLeft: filteredItems.length });
    console.log(filteredItems.length);
  };

  render() {
    return (
      <div className="App">
        <Particles className="particles" params={particlesOptions} />
        <header>
          <form id="to-do-form" onSubmit={this.addItem}>
            <input
              type="text"
              placeholder="Enter Task"
              value={this.state.currentItem.text}
              onChange={this.handelInput}
            />
            <button className="fas fa-plus"></button>
          </form>
          <label>
            {this.state.countTaskLeft === this.state.items.length
              ? this.state.countTaskLeft
              : this.state.countTaskLeft}{" "}
            task of {this.state.items.length} is left
          </label>
        </header>
        <ListItems
          items={this.state.items}
          deleteItem={this.deleteItem}
          setUpdate={this.setUpdate}
          finishItem={this.finishItem}
        />
      </div>
    );
  }
}

export default App;
